export default class viewLanding {

  static createLandingPage() {
    viewLanding.createLandingHeader();
    viewLanding.createLandingMain();
    viewLanding.createLandingFooter();
  }

  static createLandingHeader() {
    const header = document.createElement('header');
    header.className = 'header-landing';
    document.body.appendChild(header);
    viewLanding.createTitle();
    viewLanding.createNav();
  }

  static createTitle() {
    const title = document.createElement('h1');
    title.className = 'title-landing';
    title.innerHTML = 'Piskel Clone';
    document.querySelector('.header-landing').appendChild(title);
  }

  static createNav() {
    const nav = document.createElement('nav');
    nav.className = 'navigation';
    document.querySelector('.header-landing').appendChild(nav);
    viewLanding.createNavList();
  }

  static createNavList() {
    const navList = document.createElement('ul');
    navList.className = 'navigation-list';
    document.querySelector('.navigation').appendChild(navList);
    viewLanding.createStart();
    viewLanding.createScreens();
    viewLanding.createInfo();
    viewLanding.createDeveloper();
  }

  static createStart() {
    const startLi = document.createElement('li');
    startLi.className = 'navigation-list-start';
    document.querySelector('.navigation-list').appendChild(startLi);

    const start = document.createElement('a');
    start.className = 'navigation-list-link';
    start.href = '#start';
    start.innerHTML = 'Start';
    startLi.appendChild(start);
  }

  static createScreens() {
    const screensLi = document.createElement('li');
    screensLi.className = 'navigation-list-screens';
    document.querySelector('.navigation-list').appendChild(screensLi);

    const screens = document.createElement('a');
    screens.className = 'navigation-list-link';
    screens.href = '#screenshots';
    screens.innerHTML = 'Screenshots';
    screensLi.appendChild(screens);
  }

  static createInfo() {
    const infoLi = document.createElement('li');
    infoLi.className = 'navigation-list-info';
    document.querySelector('.navigation-list').appendChild(infoLi);

    const info = document.createElement('a');
    info.className = 'navigation-list-link';
    info.href = '#information';
    info.innerHTML = 'Information';
    infoLi.appendChild(info);
  }

  static createDeveloper() {
    const developerLi = document.createElement('li');
    developerLi.className = 'navigation-list-info';
    document.querySelector('.navigation-list').appendChild(developerLi);

    const developer = document.createElement('a');
    developer.className = 'navigation-list-link';
    developer.gref = '#developer';
    developer.innerHTML = 'Developer';
    developerLi.appendChild(developer);
  }

  static createLandingMain() {
    const main = document.createElement('main');
    main.className = 'main-landing';
    document.body.appendChild(main);
    viewLanding.createSectionForStart();
    viewLanding.createScreensSection();
    viewLanding.createInfoSection();
    viewLanding.createDeveloperSection();
  }

  static createSectionForStart() {
    const startSection = document.createElement('section');
    startSection.className = 'start';
    startSection.id = 'start';
    document.querySelector('.main-landing').appendChild(startSection);

    const cont = document.createElement('div');
    cont.className = 'start-container';
    startSection.appendChild(cont);
    viewLanding.createPiskelInfo();
    viewLanding.createStartButton();
    viewLanding.createExampleImg();
  }

  static createPiskelInfo() {
    const piskelInfo = document.createElement('p');
    piskelInfo.className = 'start-text';
    piskelInfo.innerHTML = 'Piskel is a free online editor for animated sprites & pixel art.';
    document.querySelector('.start-container').appendChild(piskelInfo);
  }

  static createStartButton() {
    const startButton = document.createElement('button');
    startButton.className = 'start-button';
    startButton.innerHTML = 'Create Sprite';
    document.querySelector('.start-container').appendChild(startButton);
  }

  static createExampleImg() {
    const example = document.createElement('img');
    example.className = 'start-image';
    example.src = '/./piskel-clone/src/screens/landing-page/images/Screen_of_application(All).png';
    document.querySelector('.start').appendChild(example);
  }

  static createScreensSection() {
    const screensSection = document.createElement('section');
    screensSection.className = 'screens';
    screensSection.id = 'screenshots';
    document.querySelector('.main-landing').appendChild(screensSection);
    viewLanding.createScreensTitle();
    viewLanding.createScreensList();
    viewLanding.createExampleLink();
  }

  static createScreensTitle() {
    const scrTitle = document.createElement('h2');
    scrTitle.className = 'screens-title';
    scrTitle.innerHTML = 'Screenshots of <a class="screens-title-link" href="https://vorobeykirill.github.io/piskel-clone-editor/">application</a>';
    document.querySelector('.screens').appendChild(scrTitle);
  }

  static createScreensList() {
    const list = document.createElement('ul');
    list.className = 'screens-list';
    document.querySelector('.screens').appendChild(list);

    viewLanding.createScreensImages('Screen2.png');
    viewLanding.createScreensImages('Screen_of_application(All).png');
    viewLanding.createScreensImages('Screen3.png');
  }

  static createScreensImages(link) {
    const el = document.createElement('li');
    document.querySelector('.screens-list').appendChild(el);

    const img = document.createElement('img');
    img.className = 'screens-list-images';
    img.src = `/./piskel-clone/src/screens/landing-page/images/${link}`;
    el.appendChild(img);
  }

  static createExampleLink() {
    const link = document.createElement('a');
    link.className = 'screens-example';
    link.href = 'https://www.piskelapp.com/p/agxzfnBpc2tlbC1hcHByEwsSBlBpc2tlbBiAgIDvjNGKCAw/edit';
    link.target = '_blanket';
    link.innerHTML = 'Example of animation';
    document.querySelector('.screens').appendChild(link);
  }

  static createInfoSection() {
    const info = document.createElement('section');
    info.className = 'information';
    info.id = 'information';
    document.querySelector('.main-landing').appendChild(info);
    viewLanding.createInfoTitle();
    viewLanding.createInfoDrawingList();
    viewLanding.createOtherToolsList();
  }

  static createInfoTitle() {
    const infoTitle = document.createElement('h2');
    infoTitle.className = 'information-title';
    infoTitle.innerHTML = 'Information about application';
    document.querySelector('.information').appendChild(infoTitle);
  }

  static createInfoDrawingList() {
    const listTitle = document.createElement('h3');
    listTitle.className = 'information-list-drawing-title'
    listTitle.innerHTML = 'Drawing tools';
    document.querySelector('.information').appendChild(listTitle);

    const infoList = document.createElement('ul');
    infoList.className = 'information-list-drawing';
    document.querySelector('.information').appendChild(infoList);

    viewLanding.createInfoDrawingElements('Pen tool');
    viewLanding.createInfoDrawingElements('Eraser tool');
    viewLanding.createInfoDrawingElements('Vertical mirror drawing');
    viewLanding.createInfoDrawingElements('Paint all pixels of the same color');
    viewLanding.createInfoDrawingElements('Color picker');
    viewLanding.createInfoDrawingElements('Drawing line');
    viewLanding.createInfoDrawingElements('Drawing rectangle');
    viewLanding.createInfoDrawingElements('Drawing circle');
    viewLanding.createInfoDrawingElements('Lighten');
  }

  static createInfoDrawingElements(text) {
    const el = document.createElement('li');
    document.querySelector('.information-list-drawing').appendChild(el);

    const infoElem = document.createElement('p');
    infoElem.className = 'information-list-drawing-element';
    infoElem.innerHTML = `${text}`;
    el.appendChild(infoElem);
  }

  static createOtherToolsList() {
    const listTitle = document.createElement('h3');
    listTitle.className = 'information-list-other-title'
    listTitle.innerHTML = 'Other functionality';
    document.querySelector('.information').appendChild(listTitle);

    const infoList = document.createElement('ul');
    infoList.className = 'information-list-other';
    document.querySelector('.information').appendChild(infoList);

    viewLanding.createOtherToolsElements('Frame managment: ability to copy, delete and add new frames');
    viewLanding.createOtherToolsElements('Resizing drawing area');
    viewLanding.createOtherToolsElements('Preview field with the possibility of changing fps');
  }

  static createOtherToolsElements(text) {
    const el = document.createElement('li');
    document.querySelector('.information-list-other').appendChild(el);

    const infoElem = document.createElement('p');
    infoElem.className = 'information-list-other-element';
    infoElem.innerHTML = `${text}`;
    el.appendChild(infoElem);
  }

  static createDeveloperSection() {
    const developer = document.createElement('section');
    developer.className = 'developer';
    developer.id = 'developer';
    document.querySelector('.main-landing').appendChild(developer);
    viewLanding.createDeveloperTitle();
    viewLanding.createDeveloperName();
    viewLanding.createDeveloperPhoto();
    viewLanding.createDeveloperSocialsList();
  }

  static createDeveloperTitle() {
    const devTitle = document.createElement('h2');
    devTitle.className = 'developer-title';
    devTitle.innerHTML = 'Application developer'
    document.querySelector('.developer').appendChild(devTitle);
  }

  static createDeveloperName() {
    const devName = document.createElement('h3');
    devName.className = 'developer-name';
    devName.innerHTML = 'Kirill Vorobey';
    document.querySelector('.developer').appendChild(devName);
  }

  static createDeveloperPhoto() {
    const devPhoto = document.createElement('img');
    devPhoto.className = 'developer-photo';
    devPhoto.src = '/./piskel-clone/src/screens/landing-page/images/developer.jpg';
    document.querySelector('.developer').appendChild(devPhoto);
  }

  static createDeveloperSocialsList() {
    const socialList = document.createElement('ul');
    socialList.className = 'developer-socials';
    document.querySelector('.developer').appendChild(socialList);
    viewLanding.createTelegramLink();
    viewLanding.createGitLink();
  }

  static createTelegramLink() {
    const telegramLi = document.createElement('li');
    telegramLi.className = 'developer-socials-telegram';
    document.querySelector('.developer-socials').appendChild(telegramLi);

    const telegram = document.createElement('a');
    telegram.className = 'developer-socials-link'
    telegram.href = 'https://t.me/Vorobey_ll';
    telegramLi.appendChild(telegram);

    const telegramImage = document.createElement('img');
    telegramImage.className = 'developer-socials-image';
    telegramImage.src = '/./piskel-clone/src/screens/landing-page/images/telegram.png';
    telegram.appendChild(telegramImage);
  }

  static createGitLink() {
    const gitLi = document.createElement('li');
    gitLi.className = 'developer-socials-git';
    document.querySelector('.developer-socials').appendChild(gitLi);

    const git = document.createElement('a');
    git.className = 'developer-socials-link'
    git.href = 'https://github.com/VorobeyKirill';
    gitLi.appendChild(git);

    const gitImage = document.createElement('img');
    gitImage.className = 'developer-socials-image';
    gitImage.src = '/./piskel-clone/src/screens/landing-page/images/github.png';
    git.appendChild(gitImage);
  }

  static createLandingFooter() {
    const footer = document.createElement('footer');
    footer.className = 'footer';
    document.body.appendChild(footer);
    viewLanding.createFooterText();
  }

  static createFooterText() {
    const footerText = document.createElement('p');
    footerText.className = 'footer-text';
    footerText.innerHTML = 'Kirill Vorobey 2019';
    document.querySelector('.footer').appendChild(footerText);
  }

  static removeLandingPage() {
    document.querySelector('.header-landing').remove();
    document.querySelector('.main-landing').remove();
    document.querySelector('.footer').remove();
  }
}
