export default class viewFrames {

  static createHeader() {
    const header = document.createElement('header');
    header.className = 'header';
    document.body.appendChild(header);

    const title = document.createElement('h1');
    title.className = 'title';
    title.innerHTML = 'Piskel';
    header.appendChild(title);
  }

  static createMain() {
    const main = document.createElement('main');
    main.className = 'main';
    document.body.appendChild(main);
  }

  static createFramesList() {
    const framesColumn = document.createElement('div');
    framesColumn.className = 'frames-column';
    document.querySelector('.main').appendChild(framesColumn);

    const framesList = document.createElement('ul');
    framesList.className = 'frames-list';
    framesColumn.appendChild(framesList);
  }

  static createFrame(length) {
    const frame = document.createElement('li');
    frame.className = 'frame';
    frame.classList.add(`frame${length}`);
    document.querySelector('.frames-list').appendChild(frame);
    viewFrames.createCopyButton(length);
    viewFrames.createFrameCanvas(length);
  }

  static createFrameCanvas(length) {
    const frameCanvas = document.createElement('canvas');
    frameCanvas.className = 'frame-canvas';
    frameCanvas.classList.add(`frame-canvas${length}`);
    document.querySelector('.frame').appendChild(frameCanvas);
  }

  static createCopyButton(length) {
    const copy = document.createElement('button');
    copy.className = 'duplicate-frame';
    copy.classList.add(`duplicate-frame${length}`);
    document.querySelector('.frame').appendChild(copy);
  }

  static createDeleteButton(length) {
    const deleteFrame = document.createElement('button');
    deleteFrame.className = 'delete-frame';
    deleteFrame.classList.add(`delete-frame${length}`);
    document.querySelector('.frame').appendChild(deleteFrame);
  }

  static createAddNewFrameButton() {
    const addButton = document.createElement('div');
    addButton.className = 'add-frame';
    addButton.innerHTML = 'Add New Frame'
    document.querySelector('.frames-column').appendChild(addButton);
  }
}
