import Canvas from "../../screens/canvas/canvas";

export default class Tools {

  static toolFocus(e) {
    [].forEach.call(document.getElementsByClassName('tools-canvas'), el => el.style.border = '2px solid aqua');
    e.target.style.border = '2px solid red';
  }

  static toolFocusByKeyDown(tool) {
    [].forEach.call(document.getElementsByClassName('tools-canvas'), el => el.style.border = '2px solid aqua');
    tool.style.border = '2px solid red';
  }

  static canvasResizeFocus(e) {
    [].forEach.call(document.getElementsByClassName('tools-resize'), el => el.style.border = 'none');
    e.target.style.border = '2px solid red';
  }

  static canvasResizeFocusByKeyDown(size) {
    [].forEach.call(document.getElementsByClassName('tools-resize'), el => el.style.border = 'none');
    size.style.border = '2px solid red';
  }

  static toolsControl() {
    Canvas.canvasSize = 'canvas32';
    Canvas.pen = 'penTool';
    Canvas.eraser = 'eraserTool';
    Canvas.mirrorPen = 'mirrorPenTool';
    Canvas.colorPicker = 'colorPickerTool';
    Canvas.paintAllSame = 'paintAllSameTool';
    Canvas.lighten = 'lightenTool';
    Canvas.line = 'lineTool';
    Canvas.rectangle = 'rectangleTool';
    Canvas.circle = 'circleTool';
    Canvas.canvas32 = 'canvas32';
    Canvas.canvas64 = 'canvas64';
    Canvas.canvas128 = 'canvas128';
    Canvas.drawingListener = false;
    Canvas.cleaningListener = false;
    Canvas.drawingMirrorListener = false;
    Canvas.colorPickerListener = false;
    Canvas.paintAllPixelsListener = false;
    Canvas.lightenListener = false;
    Canvas.lineListener = false;
    Canvas.rectangleListener = false;
    Canvas.isDown = false;

    const tools = document.querySelector('.tools');
    const pen = document.querySelector('.tools-pen');
    const eraser = document.querySelector('.tools-eraser');
    const mirrorPen = document.querySelector('.tools-mirrorPen');
    const colorPicker = document.querySelector('.tools-colorPicker');
    const paintAllSame = document.querySelector('.tools-paintAllSame');
    const lighten = document.querySelector('.tools-lighten');
    const line = document.querySelector('.tools-line');
    const circle = document.querySelector('.tools-circle');
    const rectangle = document.querySelector('.tools-rectangle');
    const canvas32 = document.querySelector('.tools-resize-canvas-32');
    const canvas64 = document.querySelector('.tools-resize-canvas-64');
    const canvas128 = document.querySelector('.tools-resize-canvas-128');
    const coordinates = document.querySelector('.canvas-coordinates-size');

    tools.addEventListener('click', (e) => {
      switch (e.target) {
        case (pen):
          Tools.toolFocus(e);
          Canvas.currentTool = 'penTool';
          Canvas.canvasControl();
          break;
        case (eraser):
          Tools.toolFocus(e);
          Canvas.currentTool = 'eraserTool';
          Canvas.canvasControl();
          break;
        case (mirrorPen):
          Tools.toolFocus(e);
          Canvas.currentTool = 'mirrorPenTool';
          Canvas.canvasControl();
          break;
        case (colorPicker):
          Tools.toolFocus(e);
          Canvas.currentTool = 'colorPickerTool';
          Canvas.canvasControl();
          break;
        case (paintAllSame):
          Tools.toolFocus(e);
          Canvas.currentTool = 'paintAllSameTool';
          Canvas.canvasControl();
          break;
        case (lighten):
          Tools.toolFocus(e);
          Canvas.currentTool = 'lightenTool';
          Canvas.canvasControl();
          break;
        case (line):
          Tools.toolFocus(e);
          Canvas.currentTool = 'lineTool';
          Canvas.canvasControl();
          break;
        case (circle):
          Tools.toolFocus(e);
          Canvas.currentTool = 'circleTool';
          Canvas.canvasControl();
          break;
        case (rectangle):
            Tools.toolFocus(e);
            Canvas.currentTool = 'rectangleTool';
            Canvas.canvasControl();
            break;
        case (canvas32):
          Tools.canvasResizeFocus(e);
          coordinates.innerHTML = '[32x32]';
          Canvas.canvasSize = 'canvas32';
          Canvas.canvasControl();
          break;
        case (canvas64):
          Tools.canvasResizeFocus(e);
          coordinates.innerHTML = '[64x64]';
          Canvas.canvasSize = 'canvas64';
          Canvas.canvasControl();
          break;
        case (canvas128):
          Tools.canvasResizeFocus(e);
          coordinates.innerHTML = '[128x128]';
          Canvas.canvasSize = 'canvas128';
          Canvas.canvasControl();
          break;
      }
    });

    document.addEventListener('keydown', (e) => {
      switch (e.code) {
        case ('KeyP'):
          Tools.toolFocusByKeyDown(pen);
          Canvas.currentTool = 'penTool';
          Canvas.canvasControl();
          break;
        case ('KeyE'):
          Tools.toolFocusByKeyDown(eraser);
          Canvas.currentTool = 'eraserTool';
          Canvas.canvasControl();
          break;
        case ('KeyM'):
            Tools.toolFocusByKeyDown(mirrorPen);
            Canvas.currentTool = 'mirrorPenTool';
            Canvas.canvasControl();
            break;
        case ('KeyO'):
          Tools.toolFocusByKeyDown(colorPicker);
          Canvas.currentTool = 'colorPickerTool';
          Canvas.canvasControl();
          break;
        case ('KeyX'):
          Tools.toolFocusByKeyDown(paintAllSame);
          Canvas.currentTool = 'paintAllSameTool';
          Canvas.canvasControl();
          break;
        case ('KeyG'):
          Tools.toolFocusByKeyDown(lighten);
          Canvas.currentTool = 'lightenTool';
          Canvas.canvasControl();
          break;
        case ('KeyL'):
          Tools.toolFocusByKeyDown(line);
          Canvas.currentTool = 'lineTool';
          Canvas.canvasControl();
          break;
        case ('KeyC'):
          Tools.toolFocusByKeyDown(circle);
          Canvas.currentTool = 'circleTool';
          Canvas.canvasControl();
          break;
        case ('KeyR'):
          Tools.toolFocusByKeyDown(rectangle);
          Canvas.currentTool = 'rectangleTool';
          Canvas.canvasControl();
          break;
        case ('Digit1'):
          Tools.canvasResizeFocusByKeyDown(canvas32);
          coordinates.innerHTML = '[32x32]';
          Canvas.canvasSize = 'canvas32';
          Canvas.canvasControl();
          break;
        case ('Digit2'):
          Tools.canvasResizeFocusByKeyDown(canvas64);
          coordinates.innerHTML = '[64x64]';
          Canvas.canvasSize = 'canvas64';
          Canvas.canvasControl();
          break;
        case ('Digit3'):
          Tools.canvasResizeFocusByKeyDown(canvas128);
          coordinates.innerHTML = '[128x128]';
          Canvas.canvasSize = 'canvas128';
          Canvas.canvasControl();
          break;
      }
    });
  }
}
