
import viewFrames from "./src/components/frames-list/style";
import viewTools from "./src/components/tools/style";
import viewCanvas from "./src/screens/canvas/style";
import FramesControl from "./src/components/frames-list/frames";
import viewPrev from "./src/screens/preview/style";
import viewLanding from "./src/screens/landing-page/style";
import Tools from "./src/components/tools/tools";
import Canvas from "./src/screens/canvas/canvas";

viewLanding.createLandingPage();

document.querySelector('.start-button').addEventListener('click', () => {
  viewLanding.removeLandingPage();

  viewFrames.createHeader();
  viewFrames.createMain();
  viewTools.createToolContainer();
  viewFrames.createFramesList();
  viewFrames.createFrame(1);
  viewFrames.createAddNewFrameButton();
  viewCanvas.createCanvasContainer();
  viewPrev.createPreviewContainer();

  Canvas.checkCursorCoordinates();
  Canvas.deleteDefaultActions();
  Canvas.setCanvasDimensions();
  FramesControl.controlFrame();
  Tools.toolsControl();
});
