export default class viewCanvas {

  static createCanvasContainer() {
    const cont = document.createElement('div');
    cont.className = 'canvas-container';
    document.querySelector('.main').appendChild(cont);
    viewCanvas.createCanvas();
    viewCanvas.createFieldForCoordinates();
    viewCanvas.createCanvasSizeText();
    viewCanvas.createCanvasCoordinateText();
  }

  static createCanvas() {
    const canv = document.createElement('canvas');
    canv.className = 'canvas-container-field';
    document.querySelector('.canvas-container').appendChild(canv);
  }

  static createFieldForCoordinates() {
    const coord = document.createElement('div');
    coord.className = 'canvas-coordinates';
    document.querySelector('.canvas-container').appendChild(coord);
  }

  static createCanvasSizeText() {
    const size = document.createElement('p');
    size.className = 'canvas-coordinates-size';
    size.innerHTML = '[32x32]';
    document.querySelector('.canvas-coordinates').appendChild(size);
  }

  static createCanvasCoordinateText() {
    const coord = document.createElement('p');
    coord.className = 'canvas-coordinates-cursor';
    coord.innerHTML = '20:20';
    document.querySelector('.canvas-coordinates').appendChild(coord);
  }
}
