export default class viewPrev {

  static createPreviewContainer() {
    const prevCont = document.createElement('div');
    prevCont.className = 'animation';
    document.querySelector('.main').appendChild(prevCont);
    viewPrev.createFullScreenButton();
    viewPrev.createPreview();
    viewPrev.createFpsInput();
  }

  static createPreview() {
    const prev = document.createElement('div');
    prev.className = 'animation-field';
    document.querySelector('.animation').appendChild(prev);
  }

  static createFpsInput() {
    const fps = document.createElement('input');
    fps.className = 'animation-fps';
    fps.type = 'range';
    fps.min = '1';
    fps.max = '24';
    document.querySelector('.animation').appendChild(fps);
  }

  static createFullScreenButton() {
    const fullScreen = document.createElement('button');
    fullScreen.className = 'full-screen';
    document.querySelector('.animation').appendChild(fullScreen);

    const tooltip = document.createElement('span');
    tooltip.className = 'full-screen-tooltip';
    tooltip.innerHTML = 'Full screen  (F)';
    fullScreen.appendChild(tooltip);
  }
}
