export default class viewTools {

  static createToolContainer() {
    const container = document.createElement('div');
    container.className = 'tools';
    document.querySelector('.main').appendChild(container);

    viewTools.createPen();
    viewTools.createEraser();
    viewTools.createMirrorPen();
    viewTools.createColorPicker();
    viewTools.createPaintSameColorPixels();
    viewTools.createLighten();
    viewTools.createLine();
    viewTools.createCircle();
    viewTools.createRectangle();
    viewTools.createColorPrimary();
    viewTools.createColorSecondary();
    viewTools.createResizeCanvasText();
    viewTools.createResizeCanvasButton32();
    viewTools.createResizeCanvasButton64();
    viewTools.createResizeCanvasButton128();
  }

  static createPen() {
    const pen = document.createElement('div');
    pen.className = 'tools-pen';
    pen.classList.add('tools-canvas');
    document.querySelector('.tools').appendChild(pen);

    const tooltip = document.createElement('span');
    tooltip.className = 'tools-pen-tooltip';
    tooltip.innerHTML = 'Pen tool (P)';
    pen.appendChild(tooltip);
  }

  static createEraser() {
    const eraser = document.createElement('div');
    eraser.className = 'tools-eraser';
    eraser.classList.add('tools-canvas');
    document.querySelector('.tools').appendChild(eraser);

    const tooltip = document.createElement('span');
    tooltip.className = 'tools-eraser-tooltip';
    tooltip.innerHTML = 'Eraser tool (E)';
    eraser.appendChild(tooltip);
  }

  static createMirrorPen() {
    const mirror = document.createElement('div');
    mirror.className = 'tools-mirrorPen';
    mirror.classList.add('tools-canvas');
    document.querySelector('.tools').appendChild(mirror);

    const tooltip = document.createElement('span');
    tooltip.className = 'tools-mirrorPen-tooltip';
    tooltip.innerHTML = 'Vertical mirror pen (V)';
    mirror.appendChild(tooltip);
  }

  static createColorPicker() {
    const picker = document.createElement('div');
    picker.className = 'tools-colorPicker';
    picker.classList.add('tools-canvas');
    document.querySelector('.tools').appendChild(picker);

    const tooltip = document.createElement('span');
    tooltip.className = 'tools-colorPicker-tooltip';
    tooltip.innerHTML = 'Color picker (O)';
    picker.appendChild(tooltip);
  }

  static createPaintSameColorPixels() {
    const paint = document.createElement('div');
    paint.className = 'tools-paintAllSame';
    paint.classList.add('tools-canvas');
    document.querySelector('.tools').appendChild(paint);

    const tooltip = document.createElement('span');
    tooltip.className = 'tools-paintAllSame-tooltip';
    tooltip.innerHTML = 'Paint all pixels of the same color (X)';
    paint.appendChild(tooltip);
  }

  static createLighten() {
    const light = document.createElement('div');
    light.className = 'tools-lighten';
    light.classList.add('tools-canvas');
    document.querySelector('.tools').appendChild(light);

    const tooltip = document.createElement('span');
    tooltip.className = 'tools-lighten-tooltip';
    tooltip.innerHTML = 'Lighten (G)';
    light.appendChild(tooltip);
  }

  static createLine() {
    const line = document.createElement('div');
    line.className = 'tools-line';
    line.classList.add('tools-canvas');
    document.querySelector('.tools').appendChild(line);

    const tooltip = document.createElement('span');
    tooltip.className = 'tools-line-tooltip';
    tooltip.innerHTML = 'Line tool (L)';
    line.appendChild(tooltip);
  }

  static createCircle() {
    const circle = document.createElement('div');
    circle.className = 'tools-circle';
    circle.classList.add('tools-canvas');
    document.querySelector('.tools').appendChild(circle);

    const tooltip = document.createElement('span');
    tooltip.className = 'tools-circle-tooltip';
    tooltip.innerHTML = 'Circle tool (C)';
    circle.appendChild(tooltip);
  }

  static createRectangle() {
    const rect = document.createElement('div');
    rect.className = 'tools-rectangle';
    rect.classList.add('tools-canvas');
    document.querySelector('.tools').appendChild(rect);

    const tooltip = document.createElement('span');
    tooltip.className = 'tools-rectangle-tooltip';
    tooltip.innerHTML = 'Rectangle tool (R)';
    rect.appendChild(tooltip);
  }

  static createColorPrimary() {
    const color = document.createElement('input');
    color.className = 'tools-color-primary';
    color.type = 'color';
    color.value = '#ff0000';
    document.querySelector('.tools').appendChild(color);
  }

  static createColorSecondary() {
    const color = document.createElement('input');
    color.className = 'tools-color-secondary';
    color.type = 'color';
    color.value = '#00ff00';
    document.querySelector('.tools').appendChild(color);
  }

  static createResizeCanvasText() {
    const text = document.createElement('p');
    text.className = 'tools-resize-canvas-text';
    text.innerHTML = 'Resize the drawing area';
    document.querySelector('.tools').appendChild(text);
  }

  static createResizeCanvasButton32() {
    const button = document.createElement('button');
    button.className = 'tools-resize-canvas-32';
    button.classList.add('tools-resize');
    button.innerHTML = '32x32';
    document.querySelector('.tools').appendChild(button);

    const tooltip = document.createElement('span');
    tooltip.className = 'tools-resize-tooltip-32';
    tooltip.innerHTML = 'Resize canvas 32x32  (1)';
    button.appendChild(tooltip);
  }

  static createResizeCanvasButton64() {
    const button = document.createElement('button');
    button.className = 'tools-resize-canvas-64';
    button.classList.add('tools-resize');
    button.innerHTML = '64x64';
    document.querySelector('.tools').appendChild(button);

    const tooltip = document.createElement('span');
    tooltip.className = 'tools-resize-tooltip-64';
    tooltip.innerHTML = 'Resize canvas 64x64  (2)';
    button.appendChild(tooltip);
  }

  static createResizeCanvasButton128() {
    const button = document.createElement('button');
    button.className = 'tools-resize-canvas-128';
    button.classList.add('tools-resize');
    button.innerHTML = '128x128';
    document.querySelector('.tools').appendChild(button);

    const tooltip = document.createElement('span');
    tooltip.className = 'tools-resize-tooltip-128';
    tooltip.innerHTML = 'Resize canvas 128x128  (3)';
    button.appendChild(tooltip);
  }
}
