export default class Canvas {
  constructor(currentTool, canvasSize) {
    this.currentTool = currentTool;
    this.pen = 'penTool';
    this.eraser = 'eraserTool';
    this.mirrorPen = 'mirrorPenTool';
    this.colorPicker = 'colorPickerTool';
    this.paintAllSame = 'paintAllSameTool';
    this.lighten = 'lightenTool';
    this.line = 'lineTool';
    this.circle = 'circleTool';
    this.rectangle = 'rectangleTool';
    this.canvasSize = canvasSize;
    this.canvas32 = 'canvas32';
    this.canvas64 = 'canvas64';
    this.canvas128 = 'canvas128';
    this.drawingListener = false;
    this.cleaningListener = false;
    this.drawingMirrorListener = false;
    this.colorPickerListener = false;
    this.paintAllPixelsListener = false;
    this.lightenListener = false;
    this.lineListener = false;
    this.circleListener = false;
    this.rectangleListener = false;
    this.isDown = false;
  }

  static setCanvasDimensions() {
    const width = document.querySelector('.canvas-container-field').offsetWidth;
    const height = document.querySelector('.canvas-container-field').offsetHeight;
    const canvas = document.querySelector('.canvas-container-field');

    canvas.height = height;
    canvas.width = width;
  }

 static canvasSizeControl() {
    let size;
    switch (this.canvasSize) {
      case (this.canvas32):
        size = 32;
        return size;
      case (this.canvas64):
        size = 64;
        return size;
      case (this.canvas128):
        size = 128;
        return size;
    }
  }

  static canvasControl() {
    switch (this.currentTool) {
      case (Canvas.pen):
        Canvas.drawing();
        break;
      case (Canvas.eraser):
        Canvas.cleaning();
        break;
      case (Canvas.mirrorPen):
        Canvas.mirrorDrawing();
        break;
      case (Canvas.colorPicker):
        Canvas.colorPick();
        break;
      case (Canvas.paintAllSame):
        Canvas.paintAllPixelsOfSameColor();
        break;
      case (Canvas.lighten):
        Canvas.lightenPixel();
        break;
      case (Canvas.line):
        Canvas.drawLine();
        break;
      case (Canvas.circle):
        Canvas.drawCircle();
        break;
      case (Canvas.rectangle):
        Canvas.drawRectangle();
        break;
    }
  }

  static deleteDefaultActions() {
    window.addEventListener('contextmenu', (e) => {
      e.preventDefault();
    });
  }

  static checkCursorCoordinates() {
    const canvas = document.querySelector('.canvas-container-field');
    const coordinates = document.querySelector('.canvas-coordinates-cursor');

    canvas.addEventListener('mousemove', () => {
      const size = Canvas.canvasSizeControl();
      const cellSize = Math.floor(canvas.offsetHeight / size);
      const x = Math.floor(event.offsetX / cellSize);
      const y = Math.floor(event.offsetY / cellSize);
      coordinates.innerHTML = `${x}:${y}`;
    });

    canvas.addEventListener('mouseleave', () => {
      coordinates.innerHTML = '';
    });
  }

  static mouseMoveDrawing() {
    const size = Canvas.canvasSizeControl();
    const canvas = document.querySelector('.canvas-container-field');
    const context = canvas.getContext('2d');
    const cellSize = Math.floor(canvas.offsetHeight / size);
    Canvas.drawingListener = true;
    document.addEventListener('mouseup', () => {
      this.isDown = false;
    });

    canvas.addEventListener('mousedown', () => {
      this.isDown = true;
    });

    if (this.isDown) {
      const x = Math.floor(event.offsetX / cellSize);
      const y = Math.floor(event.offsetY / cellSize);

      context.beginPath();
      context.rect(x * cellSize, y * cellSize, cellSize, cellSize);

      if(event.which === 1) {
        context.fillStyle = document.querySelector('.tools-color-primary').value;
      } else if (event.which === 3) {
          context.fillStyle = document.querySelector('.tools-color-secondary').value;
        }

      context.fill();
    }
  }

  static mouseDownDrawing() {
    const size = Canvas.canvasSizeControl();
    const canvas = document.querySelector('.canvas-container-field');
    const context = canvas.getContext('2d');
    const cellSize = Math.floor(canvas.offsetHeight / size);
    Canvas.drawingListener = true;

    const x = Math.floor(event.offsetX / cellSize);
    const y = Math.floor(event.offsetY / cellSize);

    context.beginPath();
    context.rect(x * cellSize, y * cellSize, cellSize, cellSize);

    if(event.which === 1) {
      context.fillStyle = document.querySelector('.tools-color-primary').value;
    } else if (event.which === 3) {
        context.fillStyle = document.querySelector('.tools-color-secondary').value;
      }

    context.fill();
  }

  static drawing() {
    const canvas = document.querySelector('.canvas-container-field');

    Canvas.checkForListeners();

    canvas.addEventListener('mousedown', Canvas.mouseDownDrawing);
    canvas.addEventListener('mousemove', Canvas.mouseMoveDrawing);
  }

  static mouseMoveCleaning() {
    const size = Canvas.canvasSizeControl();
    const canvas = document.querySelector('.canvas-container-field');
    const context = canvas.getContext('2d');
    const cellSize = Math.floor(canvas.offsetHeight / size);
    Canvas.cleaningListener = true;
    document.addEventListener('mouseup', () => {
      this.isDown = false;
    });

    canvas.addEventListener('mousedown', () => {
      this.isDown = true;
    });

    if (this.isDown) {
      const x = Math.floor(event.offsetX / cellSize);
      const y = Math.floor(event.offsetY / cellSize);

      context.beginPath();
      context.clearRect(x * cellSize, y * cellSize, cellSize, cellSize);
    }
  }

  static mouseDownCleaning() {
    const size = Canvas.canvasSizeControl();
    const canvas = document.querySelector('.canvas-container-field');
    const context = canvas.getContext('2d');
    const cellSize = Math.floor(canvas.offsetHeight / size);
    Canvas.cleaningListener = true;

    const x = Math.floor(event.offsetX / cellSize);
    const y = Math.floor(event.offsetY / cellSize);

    context.beginPath();
    context.clearRect(x * cellSize, y * cellSize, cellSize, cellSize);
  }

  static cleaning() {
    const canvas = document.querySelector('.canvas-container-field');
    Canvas.checkForListeners();

    canvas.addEventListener('mousedown', Canvas.mouseDownCleaning);
    canvas.addEventListener('mousemove', Canvas.mouseMoveCleaning);
  }

  static mouseDownMirrorDrawingX() {
    const size = Canvas.canvasSizeControl();
    const canvas = document.querySelector('.canvas-container-field');
    const context = canvas.getContext('2d');
    const cellSize = Math.floor(canvas.offsetHeight / size);
    Canvas.drawingMirrorListener = true;

    const x = Math.floor(event.offsetX / cellSize);
    const y = Math.floor(event.offsetY / cellSize);
    const xMirror = ((size - 1) - x);

    context.beginPath();
    context.rect(x * cellSize, y * cellSize, cellSize, cellSize);
    context.rect(xMirror * cellSize, y * cellSize, cellSize, cellSize);

    if(event.which === 1) {
      context.fillStyle = document.querySelector('.tools-color-primary').value;
    } else if (event.which === 3) {
        context.fillStyle = document.querySelector('.tools-color-secondary').value;
      }

    context.fill();
  }

  static mouseMoveMirrorDrawingX() {
    const size = Canvas.canvasSizeControl();
    const canvas = document.querySelector('.canvas-container-field');
    const context = canvas.getContext('2d');
    const cellSize = Math.floor(canvas.offsetHeight / size);
    Canvas.drawingMirrorListener = true;
    document.addEventListener('mouseup', () => {
      this.isDown = false;
    });

    canvas.addEventListener('mousedown', () => {
      this.isDown = true;
    });

    if (this.isDown) {
      const x = Math.floor(event.offsetX / cellSize);
      const y = Math.floor(event.offsetY / cellSize);
      const xMirror = ((size - 1) - x);

      context.beginPath();
      context.rect(x * cellSize, y * cellSize, cellSize, cellSize);
      context.rect(xMirror * cellSize, y * cellSize, cellSize, cellSize);

      if(event.which === 1) {
        context.fillStyle = document.querySelector('.tools-color-primary').value;
      } else if (event.which === 3) {
          context.fillStyle = document.querySelector('.tools-color-secondary').value;
        }

      context.fill();
    }
  }

  static mirrorDrawing() {
    const canvas = document.querySelector('.canvas-container-field');
    Canvas.checkForListeners();

    canvas.addEventListener('mousedown', Canvas.mouseDownMirrorDrawingX);
    canvas.addEventListener('mousemove', Canvas.mouseMoveMirrorDrawingX);
  }

  static mouseDownColorPicker() {
    const canvas = document.querySelector('.canvas-container-field');
    const context = canvas.getContext('2d');
    const colorPrimary = document.querySelector('.tools-color-primary');
    const colorSecondary = document.querySelector('.tools-color-secondary');

    const x = Math.floor(event.offsetX / 5);
    const y = Math.floor(event.offsetY / 5);

    Canvas.colorPickerListener = true;

    function rgbToHex(r, g, b) {
      if (r > 255 || g > 255 || b > 255)
          throw "Invalid color component";
      return ((r << 16) | (g << 8) | b).toString(16);
    }

    const imageData = context.getImageData(x * 5, y * 5, 5, 5).data;

    const color = "#" + ("000000" + rgbToHex(imageData[0], imageData[1], imageData[2])).slice(-6);

    if(event.which === 1) {
      colorPrimary.value = color;
    } else if (event.which === 3) {
        colorSecondary.value = color;
      }
  }

  static colorPick() {
    const canvas = document.querySelector('.canvas-container-field');

    Canvas.checkForListeners();

    canvas.addEventListener('mousedown', Canvas.mouseDownColorPicker);
  }

  static mouseDownPaintAllSamePixels() {
    const canvas = document.querySelector('.canvas-container-field');
    const context = canvas.getContext('2d');
    Canvas.paintAllPixelsListener = true;
    const colorPrimary = document.querySelector('.tools-color-primary');
    const colorSecondary = document.querySelector('.tools-color-secondary');

    const x = Math.floor(event.offsetX / 5);
    const y = Math.floor(event.offsetY / 5);

    function rgbToHex(r, g, b) {
      if (r > 255 || g > 255 || b > 255)
          throw "Invalid color component";
      return ((r << 16) | (g << 8) | b).toString(16);
    }

    const imageData = context.getImageData(x * 5, y * 5, 5, 5).data;

    const color = "#" + ("000000" + rgbToHex(imageData[0], imageData[1], imageData[2])).slice(-6);


    for (let i = 0; i < 128; i += 1) {
      for (let j = 0; j < 128; j += 1) {
        const imageDataForLoop = context.getImageData(i * 5, j * 5, 5, 5).data;
        const colorInLoop = "#" + ("000000" + rgbToHex(imageDataForLoop[0], imageDataForLoop[1], imageDataForLoop[2])).slice(-6);

        if (colorInLoop === color) {
          if(event.which === 1) {
            context.beginPath();
            context.rect(i * 5, j * 5, 5, 5);
            context.fillStyle = colorPrimary.value;
            context.fill();
          } else if (event.which === 3) {
            context.beginPath();
            context.rect(i * 5, j * 5, 5 , 5);
            context.fillStyle = colorSecondary.value;
            context.fill();
            }
        }
      }
    }
  }

  static paintAllPixelsOfSameColor() {
    const canvas = document.querySelector('.canvas-container-field');

    Canvas.checkForListeners();

    canvas.addEventListener('mousedown', Canvas.mouseDownPaintAllSamePixels);
  }

  static mouseDownLighten() {
    function LightenDarkenColor(col, amt) {
      let usePound = false;

      if (col[0] === "#") {
          col = col.slice(1);
          usePound = true;
      }

      let num = parseInt(col,16);

      let r = (num >> 16) + amt;

      if (r > 255) {
        r = 255;
      } else if (r < 0) {
        r = 0;
      }

      let b = ((num >> 8) & 0x00FF) + amt;

      if (b > 255) {
        b = 255;
      } else if (b < 0) {
        b = 0;
      }

      let g = (num & 0x0000FF) + amt;

      if (g > 255) {
        g = 255;
      } else if (g < 0) {
        g = 0;
      }
      return (usePound?"#":"") + (g | (b << 8) | (r << 16)).toString(16);
    }

    const canvas = document.querySelector('.canvas-container-field');
    const size = Canvas.canvasSizeControl();
    const cellSize = Math.floor(canvas.offsetHeight / size);
    const context = canvas.getContext('2d');
    Canvas.lightenListener = true;

    const x = Math.floor(event.offsetX / cellSize);
    const y = Math.floor(event.offsetY / cellSize);

    function rgbToHex(r, g, b) {
      if (r > 255 || g > 255 || b > 255)
          throw "Invalid color component";
      return ((r << 16) | (g << 8) | b).toString(16);
    }

    const imageData = context.getImageData(x * cellSize, y * cellSize, cellSize, cellSize).data;

    const color = "#" + ("000000" + rgbToHex(imageData[0], imageData[1], imageData[2])).slice(-6);

    const lighterColor = LightenDarkenColor(color, 20);
    const darkerColor = LightenDarkenColor(color, -20);

    if(event.which === 1) {
      context.beginPath();
      context.rect(x * cellSize, y * cellSize, cellSize, cellSize);
      context.fillStyle = lighterColor;
      context.fill();
    } else if (event.which === 3) {
      context.beginPath();
      context.rect(x * cellSize, y * cellSize, cellSize, cellSize);
      context.fillStyle = darkerColor;
      context.fill();
      }
  }

  static lightenPixel() {
    const canvas = document.querySelector('.canvas-container-field');

    Canvas.checkForListeners();

    canvas.addEventListener('mousedown', Canvas.mouseDownLighten);
  }

  static setPixel(x, y) {
    const size = Canvas.canvasSizeControl();
    const canvas = document.querySelector('.canvas-container-field');
    const context = canvas.getContext('2d');
    const cellSize = Math.floor(canvas.offsetHeight / size);

    x = Math.floor(x / cellSize);
        y = Math.floor(y / cellSize);

        context.beginPath();
        context.rect(x * cellSize, y * cellSize, cellSize, cellSize);

        if(event.which === 1) {
          context.fillStyle = document.querySelector('.tools-color-primary').value;
        } else if (event.which === 3) {
          context.fillStyle = document.querySelector('.tools-color-secondary').value;
          }
        context.fill();
  }

  static mouseDownDrawLine() {
    const canvas = document.querySelector('.canvas-container-field');
    const context = canvas.getContext('2d');

    function drawLine(x1, y1, x2, y2) {

        let deltaX = Math.abs(x2 - x1);
        let deltaY = Math.abs(y2 - y1);
        let signX = x1 < x2 ? 1 : -1;
        let signY = y1 < y2 ? 1 : -1;
        let error = deltaX - deltaY;

        Canvas.setPixel(x2, y2);
        while(x1 != x2 || y1 != y2) {
            Canvas.setPixel(x1, y1);
            let error2 = error * 2;
            if (error2 > -deltaY) {
              error -= deltaY;
              x1 += signX;
            }
            if (error2 < deltaX) {
              error += deltaX;
              y1 += signY;
            }
        }
    }

    Canvas.lineListener = true;
    let isDown = true;
    const x1 = event.offsetX;
    const y1 = event.offsetY;
    let x2 = x1;
    let y2 = y1;

    Canvas.setPixel(x1, y1);

    const copy = context.getImageData(0, 0, 640,  640);

    canvas.addEventListener('mousemove', (event) => {
          if (isDown) {
              context.putImageData(copy, 0, 0);
              context.beginPath();
              x2 = event.offsetX;
              y2 = event.offsetY;
              drawLine(x1, y1, x2, y2);
          }
    });

    document.addEventListener('mouseup', () => {
          isDown = false;
    });
  }

  static drawLine() {
    const canvas = document.querySelector('.canvas-container-field');

    Canvas.checkForListeners();

    canvas.addEventListener('mousedown', Canvas.mouseDownDrawLine);
  }

  static mouseDownDrawCircle() {
    const canvas = document.querySelector('.canvas-container-field');
    const context = canvas.getContext('2d');

    function drawCircle(x, y, radius) {
      let x0 = 0;
      let y0 = radius;
      let gap = 0;
      let delta = (2 - 2 * radius);
      while (y0 >= 0)
      {
        Canvas.setPixel(x + x0, y - y0);
        Canvas.setPixel(x - x0, y - y0);
        Canvas.setPixel(x - x0, y + y0);
        Canvas.setPixel(x + x0, y + y0);
        gap = 2 * (delta + y0) - 1;
        if (delta < 0 && gap <= 0) {
          x0 += 1;
          delta += 2 * x0 + 1;
          continue;
        }
        if (delta > 0 && gap > 0) {
          y0 -= 1;
          delta -= 2 * y0 + 1;
          continue;
        }
        x0 += 1;
        delta += 2 * (x0 - y0);
        y0 -= 1;
      }
    }

    Canvas.circleListener = true;
    let isDown = true;
    const x1 = event.offsetX;
    const y1 = event.offsetY;
    let y2 = y1;
    const copy = context.getImageData(0, 0, 640,  640);

    canvas.addEventListener('mousemove', (event) => {
      if (isDown) {
        context.putImageData(copy, 0, 0);
        context.beginPath();
        y2 = event.offsetY;
        drawCircle(x1, y1, y2-y1);
      }
    });
    document.addEventListener('mouseup', () => {
      isDown = false;
    });
  }

  static drawCircle() {
    const canvas = document.querySelector('.canvas-container-field');

    Canvas.checkForListeners();

    canvas.addEventListener('mousedown', Canvas.mouseDownDrawCircle);
  }

  static mouseDownDrawRectangle() {
    const size = Canvas.canvasSizeControl();
    const canvas = document.querySelector('.canvas-container-field');
    const context = canvas.getContext('2d');
    const cellSize = Math.floor(canvas.offsetHeight / size);

    function drawRectangle (x1, y1, x2, y2) {
      x1 = Math.floor(x1 / cellSize);
      y1 = Math.floor(y1 / cellSize);
      x2 = Math.floor(x2 / cellSize);
      y2 = Math.floor(y2 / cellSize);

      context.beginPath();
      if(event.which === 1) {
        context.fillStyle = document.querySelector('.tools-color-primary').value;
      } else if (event.which === 3) {
        context.fillStyle = document.querySelector('.tools-color-secondary').value;
        }

      if (x2 < x1 || y2 < y1) {
        context.rect(x1 * cellSize, y1 * cellSize, -(x1 - x2) * cellSize, cellSize);
        context.rect(x1 * cellSize, y1 * cellSize, cellSize, -(y1 - y2) * cellSize);
        context.rect(x2 * cellSize, y1 * cellSize, cellSize, -(y1 - y2) * cellSize);
        context.rect(x1 * cellSize, y2 * cellSize, -(x1 - x2) * cellSize, cellSize);
        context.fill();
      } else {
        context.rect(x1 * cellSize, y1 * cellSize, (x2 - x1 + 1) * cellSize, cellSize);
        context.rect(x1 * cellSize, y1 * cellSize, cellSize, (y2 - y1 + 1) * cellSize);
        context.rect(x2 * cellSize, y1 * cellSize, cellSize, (y2 - y1 + 1) * cellSize);
        context.rect(x1 * cellSize, y2 * cellSize, (x2 - x1 + 1) * cellSize, cellSize);
        context.fill();
      }
    }

    Canvas.rectangleListener = true;
    let isDown = true;
    const x1 = event.offsetX;
    const y1 = event.offsetY;
    let x2 = x1;
    let y2 = y1;

    Canvas.setPixel(x1, y1);

    const copy = context.getImageData(0, 0, 640,  640);

    canvas.addEventListener('mousemove', (event) => {
      if (isDown) {
        context.putImageData(copy, 0, 0);
        context.beginPath();
        x2 = event.offsetX;
        y2 = event.offsetY;
        drawRectangle(x1, y1, x2, y2);
      }
    });

    document.addEventListener('mouseup', () => {
      isDown = false;
    });
  }

  static drawRectangle() {
    const canvas = document.querySelector('.canvas-container-field');

    Canvas.checkForListeners();

    canvas.addEventListener('mousedown', Canvas.mouseDownDrawRectangle);
  }

  static checkForListeners() {
    const canvas = document.querySelector('.canvas-container-field');

    if (Canvas.cleaningListener) {
      canvas.removeEventListener('mousedown', Canvas.mouseDownCleaning);
      canvas.removeEventListener('mousemove', Canvas.mouseMoveCleaning);
    }

    if (Canvas.drawingListener) {
      canvas.removeEventListener('mousedown', Canvas.mouseDownDrawing);
      canvas.removeEventListener('mousemove', Canvas.mouseMoveDrawing);
    }

    if (Canvas.drawingMirrorListener) {
      canvas.removeEventListener('mousedown', Canvas.mouseDownMirrorDrawingX);
      canvas.removeEventListener('mousemove', Canvas.mouseMoveMirrorDrawingX);
    }

    if (Canvas.colorPickerListener) {
      canvas.removeEventListener('mousedown', Canvas.mouseDownColorPicker);
    }

    if (Canvas.paintAllPixelsListener) {
      canvas.removeEventListener('mousedown', Canvas.mouseDownPaintAllSamePixels);
    }

    if (Canvas.lightenListener) {
      canvas.removeEventListener('mousedown', Canvas.mouseDownLighten);
    }

    if (Canvas.lineListener) {
      canvas.removeEventListener('mousedown', Canvas.mouseDownDrawLine);
    }

    if (Canvas.circleListener) {
      canvas.removeEventListener('mousedown', Canvas.mouseDownDrawCircle);
    }

    if (Canvas.rectangleListener) {
      canvas.removeEventListener('mousedown', Canvas.mouseDownDrawRectangle);
    }
  }
}

