import Preview from "../../screens/preview/preview";

export default class FramesControl {

  static controlFrame() {
    const button = document.querySelector('.add-frame');
    const framesList = document.querySelector('.frames-list');
    const Frames = [1];

    Preview.previewControl(Frames);

    button.addEventListener('click', () => {
      FramesControl.createNewFrame(Frames);
      FramesControl.checkForAddingDeleteToFirst(Frames);
    });

    framesList.addEventListener('click', (e) => {
      const target = e.target;
      switch (e.target.classList.item(0)) {
          case ('duplicate-frame'):
              FramesControl.duplicateFrame(target, Frames);
              break;
          case ('delete-frame'):
              FramesControl.deleteFrame(target, Frames);
              break;
      }
    });

    const canvas = document.querySelector('.canvas-container-field');
    const context = canvas.getContext('2d');
    const canvFrames = document.querySelector('.frames-list');
    const addFrameButton = document.querySelector('.add-frame');
    let counterOfFrames = 1;
    const FramesData = [];

    canvas.addEventListener('mouseup', () => {
      const canvFrame = document.querySelector(`.frame-canvas${counterOfFrames}`);
      const cont = canvFrame.getContext('2d');

      cont.clearRect(0, 0, 300, 150);

      cont.drawImage(canvas, 0, 0, 300, 150);

      const imageData = context.getImageData(0 , 0, canvas.offsetWidth, canvas.offsetHeight);
      FramesData[counterOfFrames - 1] = imageData;
    });

    addFrameButton.addEventListener('click', () => {
      counterOfFrames = document.getElementsByClassName('frame').length;

      [].forEach.call(document.getElementsByClassName('frame-canvas'), el => el.style.border = 'none');
      document.querySelector(`.frame-canvas${counterOfFrames}`).style.border = '2px solid red';
    });

    canvFrames.addEventListener('click', (e) => {
      switch (e.target.classList.item(0)) {
        case ('frame-canvas'):
          counterOfFrames = Number(e.target.classList.item(1).slice(e.target.classList.item(1).length - 1));

          [].forEach.call(document.getElementsByClassName('frame-canvas'), el => el.style.border = 'none');
          document.querySelector(`.frame-canvas${counterOfFrames}`).style.border = '2px solid red';

          if (FramesData[counterOfFrames - 1] === undefined) {
            context.clearRect(0, 0, canvas.offsetWidth, canvas.offsetHeight);
          } else {
            context.putImageData(FramesData[counterOfFrames - 1], 0, 0);
          }
          break;
        case ('duplicate-frame'):
          counterOfFrames = Number(e.target.classList.item(1).slice(e.target.classList.item(1).length - 1)) + 1;

          [].forEach.call(document.getElementsByClassName('frame-canvas'), el => el.style.border = 'none');
          document.querySelector(`.frame-canvas${counterOfFrames}`).style.border = '2px solid red';

          FramesData.splice(counterOfFrames - 1, 0, FramesData[counterOfFrames - 2]);

          if (FramesData[counterOfFrames - 2] === undefined) {
            context.clearRect(0, 0, canvas.offsetWidth, canvas.offsetHeight);
          } else {
            context.putImageData(FramesData[counterOfFrames - 1], 0, 0);
          }
          break;
        case ('delete-frame'): {
          const targetNumber = Number(e.target.classList.item(1).slice(e.target.classList.item(1).length - 1));
          FramesData.splice(targetNumber - 1, 1);
          if ( targetNumber === counterOfFrames && targetNumber !== 1) {
            counterOfFrames -= 1;

            if (FramesData[counterOfFrames - 1] === undefined) {
              context.clearRect(0, 0, canvas.offsetWidth, canvas.offsetHeight);
            } else {
              context.putImageData(FramesData[counterOfFrames - 1], 0, 0);
            }
          } else if (targetNumber < counterOfFrames) {
            counterOfFrames -= 1;
          } else if (targetNumber === 1 && counterOfFrames === 1) {
            counterOfFrames = 1;

            if (FramesData[counterOfFrames - 1] === undefined) {
              context.clearRect(0, 0, canvas.offsetWidth, canvas.offsetHeight);
            } else {
              context.putImageData(FramesData[counterOfFrames - 1], 0, 0);
            }
          }

          [].forEach.call(document.getElementsByClassName('frame-canvas'), el => el.style.border = 'none');
          document.querySelector(`.frame-canvas${counterOfFrames}`).style.border = '2px solid red';
          break;
        }
      }
    });
  }

  static createNewFrame(Frames) {
    const framesList = document.querySelector('.frames-list');
    const canv = document.querySelector('.canvas-container-field');
    const context = canv.getContext('2d');

    const frame = document.createElement('li');
    frame.classList.add('frame');
    framesList.appendChild(frame);

    let length = document.getElementsByClassName('frame').length;

    frame.classList.add(`frame${length}`);

    const frameCanvas = document.createElement('canvas');
    frameCanvas.classList.add('frame-canvas');
    frameCanvas.classList.add(`frame-canvas${length}`);

    FramesControl.createDeleteButton(length, frame);
    FramesControl.createCopyButton(length, frame);

    Frames.push(length);
    frame.appendChild(frameCanvas);

    context.clearRect(0, 0, canv.offsetWidth, canv.offsetHeight);
  }

  static createCopyButton(length, frame) {
    const copyButton = document.createElement('button');
    copyButton.classList.add('duplicate-frame');
    copyButton.classList.add(`duplicate-frame${length}`);
    frame.appendChild(copyButton);
  }

  static createDeleteButton(length, frame) {
    const deleteButton = document.createElement('button');
    deleteButton.classList.add('delete-frame');
    deleteButton.classList.add(`delete-frame${length}`);
    frame.appendChild(deleteButton)
  }

  static checkForAddingDeleteToFirst(Frames) {
    if (Frames.length === 2) {
        const frame = document.querySelector('.frame1');
        const deleteButton = document.createElement('button');

        deleteButton.classList.add('delete-frame');
        deleteButton.classList.add(`delete-frame1`);
        frame.insertBefore(deleteButton, document.querySelector('.duplicate-frame1'));
    }
  }

  static duplicateFrame(duplicate, Frames) {
    const framesList = document.querySelector('.frames-list');
    const canv = document.querySelector('.canvas-container-field');
    const context = canv.getContext('2d');
    const num = duplicate.classList.item(1).slice(duplicate.classList.item(1).length - 1);

    const frameCopy = document.createElement('li');
    frameCopy.classList.add('frame');

    const previous = document.querySelector(`.frame${num}`);
    framesList.insertBefore(frameCopy, previous.nextSibling);

    const next = Number(num) + 1;

    frameCopy.classList.add(`frame${next}`);

    const frameCopyCanv = document.createElement('canvas');
    frameCopyCanv.classList.add('frame-canvas');
    frameCopyCanv.classList.add(`frame-canvas${next}`);

    FramesControl.createDeleteButton(next,frameCopy);
    FramesControl.createCopyButton(next, frameCopy);
    frameCopy.appendChild(frameCopyCanv);

    Frames.splice(num, 0, next);

    for (let i = (Frames.indexOf(next) + 1); i < Frames.length; i += 1) {
      FramesControl.renamingClassesToHigher(i, Frames);
    }

    context.clearRect(0, 0, canv.offsetWidth, canv.offsetHeight);

   const previousCanv = document.querySelector(`.frame-canvas${num}`);
    const cont = frameCopyCanv.getContext('2d');
    cont.drawImage(previousCanv, 0, 0, 300, 150);

    FramesControl.checkForAddingDeleteToFirst(Frames);
  }

  static renamingClassesToHigher(i, Frames) {
    const frame = document.querySelectorAll(`.frame${Frames[i]}`)[1];
    frame.classList.remove(`frame${Frames[i]}`);

    const deleteButton = document.querySelectorAll(`.delete-frame${Frames[i]}`)[1];
    deleteButton.classList.remove(`delete-frame${Frames[i]}`);

    const copyButton = document.querySelectorAll(`.duplicate-frame${Frames[i]}`)[1];
    copyButton.classList.remove(`duplicate-frame${Frames[i]}`);

    const frameCanvas = document.querySelectorAll(`.frame-canvas${Frames[i]}`)[1];
    frameCanvas.classList.remove(`frame-canvas${Frames[i]}`);

    Frames[i] += 1;

    frame.classList.add(`frame${Frames[i]}`);
    deleteButton.classList.add(`delete-frame${Frames[i]}`);
    copyButton.classList.add(`duplicate-frame${Frames[i]}`);
    frameCanvas.classList.add(`frame-canvas${Frames[i]}`);
  }

  static deleteFrame(deleting, Frames) {
    const num = deleting.classList.item(1).slice(deleting.classList.item(1).length - 1);
    const deletedFrame = document.querySelector(`.frame${num}`);
    const index = Frames.findIndex(el => el == num);
    Frames.splice(index, 1);
    for (let i = index; i < Frames.length; i += 1) {
      FramesControl.renamingClassesToLower(i, Frames);
    }

    deletedFrame.remove();

    if ( Frames.length === 1) {
        const firstFrameDeleteButton = document.querySelector(`.delete-frame${Frames[0]}`);
        firstFrameDeleteButton.remove();
    }
  }

  static renamingClassesToLower(i, Frames) {
    const frame = document.querySelector(`.frame${Frames[i]}`);
    frame.classList.remove(`frame${Frames[i]}`);

    const deleteButton = document.querySelector(`.delete-frame${Frames[i]}`);
    deleteButton.classList.remove(`delete-frame${Frames[i]}`);

    const copyButton = document.querySelector(`.duplicate-frame${Frames[i]}`);
    copyButton.classList.remove(`duplicate-frame${Frames[i]}`);

    const frameCanvas = document.querySelector(`.frame-canvas${Frames[i]}`);
    frameCanvas.classList.remove(`frame-canvas${Frames[i]}`);

    Frames[i] -= 1;

    frame.classList.add(`frame${Frames[i]}`);
    deleteButton.classList.add(`delete-frame${Frames[i]}`);
    copyButton.classList.add(`duplicate-frame${Frames[i]}`);
    frameCanvas.classList.add(`frame-canvas${Frames[i]}`);
    }
}
